html,
body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
}

@font-face {
    font-family: "DINPro-Light";
    src: url("../fonts/DINPro-Light\ tr.ttf");
}

* {
    font-family: "DINPro-Light";
}

.main {
    height: inherit;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-content: space-between;
    // justify-content: center;
}

.header {
    display: flex;
    flex-wrap: wrap;
}

.header > section {
    flex: 1 0 35%;
    margin: 5px;
    height: 100px;
}

.logo-image {
    width: 10vh;
    height: 6.3vh;
    padding: 10vh 0 0 10vh;
}

.intro {
    font-size: 3vh;
    text-align: right;
    padding-right: 5vh;
}

a {
    color: black;
    text-decoration: none;
}

.wrap {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
}

.content {
    width: 60%;
    display: flex;
    flex-direction: column;
}

.top-row {
    max-height: 500px;
}

.about-me {
    z-index: 1;
    width: 70%;
    transform: translateY(-25vh);
}

.photo {
    z-index: 0;
    text-align: right;
}

.photo-me {
    opacity: 0.5;
    height: 50vh;
    width: 50vh;
}

.down-row {
    display: flex;
    justify-content: space-between;
}

.skills {
    width: 35%;
}

.timeline-list {
    list-style-type: square;
}

.projects {
    font-size: 2vh;
    margin-bottom: 0;
}

.projects-list {
    font-size: 1.5vh;
    margin-top: 1vh;
    font-weight: bold;
    list-style-type: none;
}

.projects-list a {
    display: inline;
    font-size: 7vh;
}

.nav {
    display: flex;
    justify-content: space-between;
    margin-top: auto;
    position: fixed;
    z-index: 1000000;
    overflow: hidden;
    bottom: 0px;
    width: 100%;
    font-size: 2vh;
}

.nav ul {
    list-style-type: none;
}

.nav__socials {
    margin-left: 10vh;
    padding-left: 0vh;
}

.nav__menu {
    margin-right: 10vh;
    text-align: right;
}

nav a {
    color: black;
    text-decoration: none;
    display: inline-block;
    position: relative;
}
nav a:after {
    background: none repeat scroll 0 0 transparent;
    bottom: 0;
    content: "";
    display: block;
    height: 2px;
    left: 50%;
    position: absolute;
    background: #69e9f5;
    transition: width 0.3s ease 0s, left 0.3s ease 0s;
    width: 0;
}
nav a:hover:after {
    width: 100%;
    left: 0;
}

.about {
